// Centralized configuration management
let cachedConfig = null;

// Function to get configuration based on environment
export function getConfig() {
  if (typeof window !== 'undefined') {
    // Client-side: ALWAYS prioritize window.__env over process.env
    const env = window.__env || {};
    
    const config = {
      // Core API configuration
      apiBaseUrl: env.apiBaseUrl,
      imageUrl: env.imageUrl,
      storeKey: env.storeKey,
      industrySlug: env.industrySlug,
      environment: env.environment,
      
      // Authentication & Social
      sellerLogin: env.sellerLogin,
      sellerRegister: env.sellerRegister,
      facebookId: env.facebookId,
      googleClientId: env.googleClientId,
      
      // Contact & Support
      salesAndSupport: env.salesAndSupport,
      copywrite: env.copywrite,
      
      // File Upload Settings
      profileImageType: env.profileImageType,
      profileImageUntype: env.profileImageUntype,
      profileImageSize: env.profileImageSize,
      profileVideoType: env.profileVideoType,
      profileVideoUntype: env.profileVideoUntype,
      profileVideoSize: env.profileVideoSize,
      
      // Business Settings
      failedOrderRetryHours: env.failedOrderRetryHours,
      backOrderPaymentMethod: env.backOrderPaymentMethod,
      spurtcommerceFacebookUrl: env.spurtcommerceFacebookUrl,
      spurtcommerceLinkedinUrl: env.spurtcommerceLinkedinUrl,
      spurtcommerceTwitterUrl: env.spurtcommerceTwitterUrl,
      spurtcommerceInstagramUrl: env.spurtcommerceInstagramUrl,
      spurtcommerceYoutubeUrl: env.spurtcommerceYoutubeUrl
    };
    
    // Debug logging
    console.log('ðŸŒ Client-side config debug:', {
      windowEnvExists: !!window.__env,
      windowEnvApiUrl: env.apiBaseUrl,
      processEnvApiUrl: process.env.NEXT_PUBLIC_SERVER_URL,
      finalApiUrl: config.apiBaseUrl,
      source: env.apiBaseUrl ? 'window.__env' : 'process.env'
    });
    
    // Warning if we're falling back to process.env
    if (!env.apiBaseUrl && process.env.NEXT_PUBLIC_SERVER_URL) {
      console.warn('âš ï¸ Client-side: window.__env.apiBaseUrl not found, falling back to process.env');
      console.warn('   This means env.js is not loading correctly or window.__env is not set');
    }
    
    return config;
  } else {
    // Server-side: try to read from runtime-config.json
    if (cachedConfig) {
      return cachedConfig;
    }
    
    try {
      const fs = require('fs');
      const path = require('path');
      const configPath = path.join(process.cwd(), 'public', 'runtime-config.json');
      
      if (fs.existsSync(configPath)) {
        const configContent = fs.readFileSync(configPath, 'utf8');
        const config = JSON.parse(configContent);
        
        // Validate that we have a proper API URL
        if (config.apiBaseUrl && config.apiBaseUrl.trim() !== '') {
       
          cachedConfig = config;
          return config;
        } else {
          console.warn('âš ï¸ Runtime config has empty apiBaseUrl, using environment variables');
        }
      }
    } catch (error) {
      console.warn('Could not load runtime config from JSON:', error.message);
    }
    
    // Fallback to environment variables
    const fallbackConfig = {
      // Core API configuration
      apiBaseUrl: process.env.NEXT_PUBLIC_SERVER_URL || '',
      imageUrl: process.env.NEXT_PUBLIC_IMAGE_URL || '',
      storeKey: process.env.NEXT_PUBLIC_STORE_KEY || '',
      industrySlug: process.env.NEXT_PUBLIC_INDUSTRY_SLUG || '',
      environment: process.env.NODE_ENV || 'development',
      
      // Authentication & Social
      sellerLogin: process.env.NEXT_PUBLIC_SELLER_LOGIN || '',
      sellerRegister: process.env.NEXT_PUBLIC_SELLER_REGISTER || '',
      facebookId: process.env.NEXT_PUBLIC_Facebook_ID || '',
      googleClientId: process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID || '',
      
      // Contact & Support
      salesAndSupport: process.env.NEXT_PUBLIC_SALES_AND_SUPPORT || '',
      copywrite: process.env.NEXT_PUBLIC_COPYWRITE || '',
      
      // File Upload Settings
      profileImageType: process.env.NEXT_PUBLIC_PROFILE_IMAGE_TYPE || '',
      profileImageUntype: process.env.NEXT_PUBLIC_PROFILE_IMAGE_UNTYPE || '',
      profileImageSize: process.env.NEXT_PUBLIC_PROFILE_IMAGE_SIZE || '',
      profileVideoType: process.env.NEXT_PUBLIC_PROFILE_VIDEO_TYPE || '',
      profileVideoUntype: process.env.NEXT_PUBLIC_PROFILE_VIDEO_UNTYPE || '',
      profileVideoSize: process.env.NEXT_PUBLIC_PROFILE_VIDEO_SIZE || '',
      
      // Business Settings
      failedOrderRetryHours: process.env.NEXT_PUBLIC_FAILED_ORDER_RETRYPAYMENT_HOURS || '',
      backOrderPaymentMethod: process.env.NEXT_PUBLIC_BACK_ORDER_PAYMENT_METHOD || '',
      demoEmail: process.env.NEXT_PUBLIC_DEMO_EMAIL || '',
      demoPassword: process.env.NEXT_PUBLIC_DEMO_PASSWORD || '',
      
      // Spurtcommerce URLs
      spurtcommerceSiteUrl: process.env.NEXT_PUBLIC_SPURTCOMMERCE_SITE_URL || '',
      spurtcommerceSellerUrl: process.env.NEXT_PUBLIC_SPURTCOMMERCE_SELLER_URL || '',
      spurtcommerceAdminUrl: process.env.NEXT_PUBLIC_SPURTCOMMERCE_ADMIN_URL || '',
      spurtcommercePricing: process.env.NEXT_PUBLIC_SPURTCOMMERCE_PRICING || '',
      spurtcommerceFacebookUrl: process.env.NEXT_PUBLIC_SPURTCOMMERCE_FACEBOOK_URL || '',
      spurtcommerceLinkedinUrl: process.env.NEXT_PUBLIC_SPURTCOMMERCE_LINKEDIN_URL || '',
      spurtcommerceTwitterUrl: process.env.NEXT_PUBLIC_SPURTCOMMERCE_TWITTER_URL || '',
      spurtcommerceInstagramUrl: process.env.NEXT_PUBLIC_SPURTCOMMERCE_INSTAGRAM_URL || '',
      spurtcommerceYoutubeUrl: process.env.NEXT_PUBLIC_SPURTCOMMERCE_YOUTUBE_URL || ''
    };
    
    console.log('âš ï¸ Server-side falling back to environment variables:', fallbackConfig.apiBaseUrl);
    cachedConfig = fallbackConfig;
    return fallbackConfig;
  }
}

// Function to wait for client-side config to be available
export function waitForClientConfig(maxWait = 5000) {
  return new Promise((resolve) => {
    if (typeof window === 'undefined') {
      resolve(getConfig());
      return;
    }
    
    const startTime = Date.now();
    
    const checkConfig = () => {
      // Check if window.__env is available
      if (window.__env && window.__env.apiBaseUrl) {
        console.log('âœ… window.__env is now available:', window.__env);
        resolve(getConfig());
        return;
      }
      
      // If we've waited too long, try to load from runtime-config.json
      if ((Date.now() - startTime) > maxWait) {
        console.warn('âš ï¸ Timeout waiting for window.__env, trying runtime-config.json');
        
        fetch('/runtime-config.json')
          .then(response => response.json())
          .then(config => {
            // Set window.__env from runtime config
            window.__env = config;
            console.log('âœ… Loaded config from runtime-config.json:', config);
            resolve(getConfig());
          })
          .catch(error => {
            console.error('âŒ Failed to load runtime config:', error);
            resolve(getConfig()); // Return empty config as last resort
          });
      } else {
        // Wait a bit more for window.__env to load
        setTimeout(checkConfig, 100);
      }
    };
    
    checkConfig();
  });
}