import { viewCartCount } from "@/redux/slices/userSlice";
import axiosInstance from "../interceptor";
import { setLoginCookies } from "../../utilities/cookies";
import ToastMessage from "@/Components/ToastMessage";

export async function BuyerSiginOtp(
  email,
  setOtpsend,
  setMailError,
  setEmailEdit,
  setOtpLoader,
  handleRoute,
  t
) {
  const data = {
    emailId: email,
  };
  const result = await axiosInstance.post(`customer/send-otp`, data);
  if (result && result.data.status == 1) {
    ToastMessage({ type: "success", message: result.data.message });
    setOtpsend(true);
  }

  if (result && result?.data.status == 0) {
    if (result.data.message == "Account Already Exist.Please Login") {
      ToastMessage({ type: "error", message: result.data.message });
      handleRoute();
    }
    setMailError(t("validationMessage.alreadyRegisteredEmail"));
    setEmailEdit(true);
  }
  setOtpLoader(false);
}

export async function CountryValue(setCountryOption) {
  const result = await axiosInstance.get(
    `list/country-list?limit=0&offset=0&keyword=&count=0`
  );
  if (result && result.data.status == 1) {
    const statedependent =
      result?.data?.data &&
      result?.data?.data?.map((data) => ({
        label: data.name,
        value: data.countryId,
      }));
    setCountryOption(statedependent);
  }
}
export async function StateValue(setStateDependent) {
  const result = await axiosInstance.get(
    `list/zone?limit=0&offset=0&keyword=&count=0&countryId=0`
  );
  if (result && result.data.status == 1) {
    setStateDependent(result.data.data);
  }
}

export async function BuyerSignup(
  email,
  otp,
  lastName,
  firstName,
  password,
  router,
  setLoader,
  setOtpError,
  setOtpEdit
) {
  const data = {
    firstName: firstName,
    lastName: lastName,
    emailId: email,
    otp: otp,
    password: password,
  };
  const result = await axiosInstance.post(`customer/register`, data);
  setLoader(false);
  if (result && result.data.status == 1) {
    ToastMessage({ type: "success", message: result.data.message });
    router.push("/buyer-login");
  }
  if (
    result &&
    result?.data?.status == 0 &&
    result?.data?.message?.includes("valid OTP")
  ) {
    setOtpError(result.data.message);
    setOtpEdit(true);
  }
}

export async function Forgotpassword(
  email,
  setStatus,
  setMailError,
  setLoader,
  setApiStatus,
  triger,
  setTriger,
  t
) {
  const result = await axiosInstance.get(
    "customer/forgot-password-link?email=" + email
  );
  setLoader(false);
  if (result && result.data.status == 1) {
    setTriger(triger + 1);
    setStatus(true);
    setApiStatus(result?.data?.status);
  }
  if (result && result?.data?.status == 0) {
    setMailError(t("validationMessage.notRegisterEmail"));
  }
}
export async function ChangePassword(oldPassword, newPassword, ApiSuccess) {
  const data = {
    oldPassword: oldPassword,
    newPassword: newPassword,
  };
  const result = await axiosInstance.post(`customer/change-password`, data);
  if (result && result?.data?.status == 1) {
    ToastMessage({ type: "success", message: result?.data?.message });
    ApiSuccess(newPassword);
  }
}

