export async function Checkout(
  paymentdata,
  dispatch,
  setBtBuyNowLoader,
  handleCouponClear,
  router
) {
  const result = await axiosInstance.post(
    `orders/customer-checkout`,
    paymentdata
  );

  if (result && result.data.status == 1) {
    handleCouponClear();
    localStorage.setItem("cartItems", JSON.stringify([]));
    dispatch(viewCartCount([]));
    router.push("/order-placed");
    ToastMessage({ type: "success", message: result.data.message });
    setBtBuyNowLoader(false);
  } else if (result && result.data.status == 3) {
    handleCouponClear();
    localStorage.setItem("cartItems", JSON.stringify([]));
    dispatch(viewCartCount([]));
    router.push("/");
    window.open(result.data.data, "_blank");
    setBtBuyNowLoader(false);
  } else {
    if (
      result &&
      result.data.message == "Please login for checkout, emailId already exist"
    ) {
      setBtBuyNowLoader(false);
    }
    setBtBuyNowLoader(false);
  }
}

export async function BackOrderCheckout(data, setBtBuyNowLoader, router) {
  const result = await axiosInstance.post(`orders/back-order-checkout`, data);
  if (result && result.data.status == 1) {
    localStorage.setItem("backOrder", JSON.stringify([]));
    router.push("/order-placed");
    ToastMessage({ type: "success", message: result.data.message });
    setBtBuyNowLoader(false);
  } else {
    ToastMessage({ type: "error", message: result.data.message });
    setBtBuyNowLoader(false);
  }
}

export async function BuyerAddAddressCheckout(
  address,
  handleClose,
  newAddresstype,
  handleAddress
) {
  const data = {
    addressType: newAddresstype,
    firstName: address.name,
    lastName: address.lname,
    address1: address.address,
    address2: address.address2,
    city: address.city,
    landmark: address.landMark,
    phoneNumber: address.number,
    postcode: address.pincode,
    countryId: address.country.value,
    zoneId: address.state.value,
    isDefault: address.defalut == true ? 1 : 0,
  };

  const result = await axiosInstance.post(`customer-address`, data);
  if (result && result.data.status == 1) {
    ToastMessage({ type: "success", message: result.data.message });
    handleClose();
    handleAddress();
  }
}

export const AddressListApi = async (handleFilterAddress) => {
  const result = await axiosInstance.get(`customer-address`);
  if (result && result.data.status == 1) {
    handleFilterAddress(result.data.data.customerAddress);
  }
};

export async function CheckoutEditAddress(
  address,
  handleClose,
  addressType,
  handleAddress
) {
  const data = {
    addressType: addressType,
    firstName: address.name,
    lastName: address.lname,
    address1: address.address,
    address2: address.address2,
    city: address.city,
    landmark: address.landMark,
    phoneNumber: address.number,
    postcode: address.pincode,
    countryId: address.country.value,
    zoneId: address.state.value,
    isDefault: address.defalut == true ? 1 : 0,
  };

  const result = await axiosInstance.put(
    `customer-address/${address.editId}`,
    data
  );
  if (result && result?.data?.status == 1) {
    handleClose();
    handleAddress();
    ToastMessage({ type: "success", message: result?.data?.message });
  }
}

export async function CustomerLogoutApi(LogoutClear) {
  const result = await axiosInstance.post(`customer/logout`);
  if (result && result.data && result.data.status == 1) {
    LogoutClear();
    ToastMessage({ type: "success", message: result.data.message });
  }
  return result?.data;
}

export async function CustomerLoginApi(obj) {
  const { data } = await axiosInstance.post(`customer/login`, obj);
  if (data && data.status == 1) {
    setLoginCookies(data, obj?.password);
    ToastMessage({ type: "success", message: data?.message });
  }
  return data;
}
