"use client";

// LocalStorage set function
export const setInLocalStorage = (key, value) => {
  localStorage?.setItem(key, JSON?.stringify(value));
};

// LocalStorage get function
export const getFromLocalStorage = (key) => {
  if (typeof window !== "undefined") {
    return JSON?.parse(localStorage?.getItem(key));
  } else {
    return null;
  }
};

export function GetCompareId() {
  if (typeof window !== "undefined") {
    const compareId = localStorage.getItem("compareId")
      ? localStorage.getItem("compareId")
      : undefined;
    if (compareId) {
      return JSON.parse(compareId);
    } else {
      return compareId;
    }
  }
}

export function GetAddonList() {
  if (typeof window !== "undefined") {
    const addon = localStorage.getItem("addon")
      ? localStorage.getItem("addon")
      : undefined;
    if (addon !== undefined && addon !== "undefined") {
      return JSON?.parse(addon);
    } else {
      return addon;
    }
  }
}

export function GetCartList() {
  if (typeof window !== "undefined") {
    const cart = localStorage.getItem("cartItems")
      ? localStorage.getItem("cartItems")
      : [];
    if (cart?.length > 0) {
      return JSON?.parse(cart);
    } else {
      return cart;
    }
  }
}

