//Redux
import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  isLoggedin: false,
  categoryname: "",
  quote: [],
  productbreadcrumb: false,
  categoryslug: "",
  quotationdetail: [],
  allcategorybread: false,
  subcatnames: "",
  subnamebreadcrums: false,
  subid: "",
  productsub: false,
  editprofile: false,
  pagegroup: false,
  language: "en",
  settingactive: "",
  langcontent: 0,
  cardpopCount: [],
  currency: {},
  allsettings: {},
  wishCount: 0,
  offsetVal: 0,
};

export const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    isLoggedinRedux: (state, action) => {
      state.isLoggedin = action.payload;
    },
    categoryname: (state, action) => {
      state.categoryname = [action.payload];
    },
    quotelist: (state, action) => {
      state.quote = action.payload;
    },
    breadcrumb: (state, action) => {
      state.productbreadcrumb = action.payload;
    },
    productslug: (state, action) => {
      state.categoryslug = action.payload;
    },
    quotationid: (state, action) => {
      state.quotationdetail = action.payload;
    },
    breadcrumallcategory: (state, action) => {
      state.allcategorybread = action.payload;
    },
    subcategoryname: (state, action) => {
      state.subcatnames = action.payload;
    },
    subcategorybread: (state, action) => {
      state.subnamebreadcrums = action.payload;
    },
    idsubs: (state, action) => {
      state.subid = action.payload;
    },
    subproduct: (state, action) => {
      state.productsub = action.payload;
    },
    editprofiletriger: (state, action) => {
      state.editprofile = action.payload;
    },
    pagegrouptriger: (state, action) => {
      state.pagegroup = action.payload;
    },
    languagetriger: (state, action) => {
      state.language = action.payload;
    },
    accountsettings: (state, action) => {
      state.settingactive = action.payload;
    },
    languagecontent: (state, action) => {
      state.langcontent = action.payload;
    },
    viewCartCount: (state, action) => {
      state.cardpopCount = action.payload;
    },
    currencyformat: (state, action) => {
      state.currency = action.payload;
    },
    storeSettings: (state, action) => {
      state.allsettings = action.payload;
    },
    getWishListCount: (state, action) => {
      state.wishCount = action.payload;
    },
    offsetCount: (state, action) => {
      state.offsetVal = action.payload;
    },
  },
});

export const {
  isLoggedinRedux,
  categoryname,
  quotelist,
  breadcrumb,
  productslug,
  quotationid,
  breadcrumallcategory,
  subcategoryname,
  subcategorybread,
  idsubs,
  subproduct,
  editprofiletriger,
  pagegrouptriger,
  languagetriger,
  accountsettings,
  languagecontent,
  viewCartCount,
  currencyformat,
  storeSettings,
  getWishListCount,
  offsetCount,
} = userSlice.actions;

export default userSlice.reducer;
