"use client";

//Api
import { imageUrl } from "@/utilities/url";
import { GetSettingList } from "@/utilities/localStorage";

//Component
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

export default function ContactSeller({
  contactSeller,
  setContactSeller,
  sellerEmail,
  handleContactSellerChange,
  handleAddAttachment,
  attachmentFile,
  handleRemoveFile,
  customerProfile,
  handleSubmitContactSeller,
  contactSellerDescripition,
  contactSellerDescripitionError,
  closeContactSeller,
  sellerCompanyName,
}) {
  const { t } = useTranslation();

  const handleFileClick = (event) => {
    event.target.value = "";
  };
  const settings = GetSettingList();

  const [langCode, setLangCode] = useState(null);

  useEffect(() => {
    const code = localStorage.getItem("languageCode");
    setLangCode(code);
  }, []);

  return (
    <div
      className={`${
        contactSeller
          ? "fixed w-full h-full px-4 sm:px-10 md:px-[120px] py-10 bg-white top-0 left-0 block z-100"
          : " hidden"
      }`}
    >
      <div className="flex items-center justify-between mb-6">
        <div>
          {!['',null,undefined].includes(settings?.storeLogoPath) && <img
            src={
              imageUrl +
              "?path=" +
              settings?.storeLogoPath +
              "&name=" +
              settings?.storeLogo +
              "&width=300&height=300"
            }
            alt="Spurtcommerce Marketplace"
            onError={({ currentTarget }) => {
              currentTarget.onerror = null;
              currentTarget.src = "/img/new-logo.svg";
              currentTarget.alt = "logo";
            }}
            className="min-w-[152px] max-sm:min-w-[30vw] h-10"
          /> }
         
        </div>
        <button onClick={closeContactSeller}>
          <img src="/img/close-round.svg" alt="close-round" />
        </button>
      </div>
      <div className="overflow-auto h-calc">
        {sellerCompanyName !== "" && (
          <h3 className="mb-4 text-xl font-normal text-black-100">
            {t("orderhistory.Contactseller")}
          </h3>
        )}
        <div className="flex sm:flex-row flex-col items-start sm:items-center space-x-2.5 bg-lightslate100 mb-4 px-4 py-5 rounded-md">
          <h6 className="text-sm text-black font-normals whitespace-nowrap">
            {t("seller.toLabel")}
          </h6>
          <div className="flex items-center space-x-2.5 w-full">
            <div className="rounded-full w-[25px] h-[25px]">
              <img
                src={
                  imageUrl +
                  "?path=" +
                  sellerEmail?.avatarPath +
                  "&name=" +
                  sellerEmail?.avatar +
                  "&width=60&height=60"
                }
                alt={sellerCompanyName}
                className="w-auto max-w-full rounded-full"
                onError={({ currentTarget }) => {
                  currentTarget.onerror = null;
                  currentTarget.src = "/img/user1.jpg";
                  currentTarget.alt = "user";
                }}
              />
            </div>
            <div className="flex items-center space-x-3">
              <div className="h-4 line-border min-w-px"></div>
              <p className="text-sm font-semibold text-black">{sellerEmail};</p>
              <p className="text-sm font-semibold text-black">
                {sellerCompanyName}
              </p>
            </div>
          </div>
        </div>
        <div className="p-4 mb-5 border rounded-md border-lightgrey">
          <div className="flex flex-col space-y-[5px] mb-5">
            <h3 className="text-base font-bold text-black">
              {t("seller.Requirements")}
            </h3>
            <p className="text-xs font-normal text-dark-gray">
              {t("seller.enterProductDetails")}
            </p>
          </div>
          <div className="w-full mb-10">
            <textarea
              value={contactSellerDescripition}
              onChange={handleContactSellerChange}
              className={
                contactSellerDescripitionError
                  ? "border-red-500 border min-h-40 rounded p-4 text-base font-normal text-black w-full focus:ring-0 focus:outline-0  "
                  : "border-gray-100 border min-h-40 rounded p-4 text-base font-normal text-black w-full focus:ring-0 focus:outline-0 "
              }
            ></textarea>
            <p className="mt-1 text-xs font-normal text-red-invalid">
              {contactSellerDescripitionError}
            </p>
          </div>
          <div>
            <div className="flex flex-col space-y-[5px] mb-5">
              <h4 className="text-base font-bold text-black">
                {t("seller.addAttachments")}
              </h4>
              <p className="text-xs font-normal text-dark-gray">
                {t("seller.addAttachmentsDesc")}
              </p>
            </div>
            <div className="flex mb-5">
              <div className="relative flex items-center gap-[5px] font-normal text-activegreen text-xs cursor-pointer animation">
                <input
                  className="hidden"
                  type="file"
                  id="fileupload"
                  onClick={handleFileClick}
                  onChange={(e) => handleAddAttachment(e)}
                />
                <label
                  htmlFor="fileupload"
                  className="flex items-center gap-[5px] cursor-pointer"
                >
                  <img src="/img/paper-clip.svg" alt="paper-clip" />
                  <p>{t("seller.addAttachments")}</p>
                </label>
              </div>
            </div>
            {attachmentFile?.length > 0 && (
              <div className="flex flex-wrap gap-6">
                {attachmentFile?.map((file, index) => (
                  <div
                    key={index}
                    className="flex justify-between items-center bg-lightslate100 p-2 w-[400px]"
                  >
                    <p className="text-sm font-normal text-black">
                      {file?.fileName}
                    </p>
                    <button
                      className=""
                      onClick={(e) => {
                        handleRemoveFile(index);
                      }}
                    >
                      <img
                        className="w-[11px] h-[11px]"
                        src="/img/close-input.svg"
                        alt="close-input"
                      />
                    </button>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
        <div className="bottom-0 left-0 absolute flex justify-center items-center bg-white shadow-top pt-[13px] pb-[18px] w-full">
          <a
            role="button"
            hrefLang={langCode}
            onClick={handleSubmitContactSeller}
            className="flex justify-center items-center bg-boldblack px-20 rounded h-[49px] font-medium text-white text-base btn"
          >
            {t("forgotpassword.Submit")}
          </a>
        </div>
      </div>
    </div>
  );
}
